.part7-font {
  font-family: "part7" !important;
  font-style: normal;
  -webkit-font-smoothing: antialiased;
  -webkit-text-stroke-width: 0.2px;
  -moz-osx-font-smoothing: grayscale;
}
.part-common {
  width: 100%;
  position: relative;
}
.part7 {
  background: url(/static/media/bg7@2x.3708e376.png) no-repeat center center;
  background-size: 100% 100%;
}
.part7 .text-wrapper {
  position: absolute;
  left: 40px;
  top: 17.9%;
  font-size: 18px;
  color: #493600;
  font-weight: 400;
}
.part7 .text-wrapper .text {
  line-height: 35px;
}
.part7 .text-wrapper .text span {
  margin: 0 5px;
  color: #1E7735;
}
.part7 .chart {
  position: absolute;
  left: 10px;
  top: 45%;
  z-index: 2;
}
