.login-index {
  width: 100%;
  height: 100%;
  overflow: hidden;
  background-color: #fffbea;
}
.login-index h4 {
  text-align: center;
  margin-top: 30%;
}
.login-index .username {
  width: 70%;
  height: 40px;
  background-color: #f1f1f1;
  font-size: 14px;
  text-indent: 1em;
  margin-top: 1rem;
  margin-left: 15%;
}
.login-index .password {
  width: 70%;
  height: 40px;
  background-color: #f1f1f1;
  font-size: 14px;
  text-indent: 1em;
  margin-top: 1rem;
  margin-left: 15%;
}
.login-index .button {
  width: 70%;
  height: 40px;
  font-size: 14px;
  margin-top: 1.5rem;
  margin-left: 15%;
}
