@font-face {
  font-family: 'part1';
  font-display: swap;
  src: url('//at.alicdn.com/t/webfont_wz9m59n5of.eot');
  /* IE9*/
  src: url('//at.alicdn.com/t/webfont_wz9m59n5of.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */ url('//at.alicdn.com/t/webfont_wz9m59n5of.woff2') format('woff2'), url('//at.alicdn.com/t/webfont_wz9m59n5of.woff') format('woff'), /* chrome、firefox */ url('//at.alicdn.com/t/webfont_wz9m59n5of.ttf') format('truetype'), /* chrome、firefox、opera、Safari, Android, iOS 4.2+*/ url('//at.alicdn.com/t/webfont_wz9m59n5of.svg#阿里汉仪智能黑体') format('svg');
