.part1-font {
  font-family: "part1" !important;
  font-style: normal;
  -webkit-font-smoothing: antialiased;
  -webkit-text-stroke-width: 0.2px;
  -moz-osx-font-smoothing: grayscale;
}
.part-common {
  position: relative;
}
.part1 {
  background: url(/static/media/bg1-01@2x.581337ff.png) no-repeat center center;
  background-size: 100% 100%;
}
.part1 .logo {
  width: 34.7%;
  height: 5%;
  position: absolute;
  left: 12px;
  top: 10px;
  background: url(/static/media/logo@2x.c02fb110.png) no-repeat center center;
  background-size: 100% 100%;
}
.part1 .text-wrapper {
  width: 33.6%;
  height: 150px;
  position: absolute;
  left: 32%;
  top: 25.3%;
}
.part1 .text-wrapper .text {
  font-size: 14px;
  text-align: center;
  color: #1E7735;
  height: 24px;
}
.part1 .text-wrapper .text span {
  color: #CFB204;
  margin-right: 5px;
}
